import React, {Component} from 'react';
import './Header.css';
import Navbar from './Navbar/Navbar';
import logo from '../../assets/images/logo_baltag_3.png';

export default class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      menuExpanded: false
    };

    this.toggleMenuExpanded = this.toggleMenuExpanded.bind(this);
  }

  toggleMenuExpanded() {
    this.setState(prevState => ({menuExpanded: !prevState.menuExpanded}));
  }

  render() {
    const logoAlt = 'Baltag Traduceri';

    return (
        <header className="mastHead">
          <div className="mobileNavBtn" onClick={this.toggleMenuExpanded}>
            <i className="fa fa-bars fa-lg"/>
          </div>

          <div className="container">
            <div className="row">
              <div className="col-lg-3 logo-parent">
                <div className="logo">
                  <a href="#">
                    <img src={logo} alt={logoAlt}/>
                  </a>
                </div>
              </div>
              <div className="col-lg-9">
                <Navbar menuExpanded={this.state.menuExpanded}/>
              </div>
            </div>
          </div>
        </header>
    );
  }
}


