import React, {Component} from 'react';
import './Navbar.css';
import NavItem from './NavItem';

export default class Navbar extends Component {
  render() {
    const menuTabs = [
      {
        name: 'Acasa',
        href: '#',
      }, {
        name: 'Servicii',
        href: '#',
        children: [
          {
            name: 'Traduceri Notariale',
            href: '#',
          }, {
            name: 'Traduceri Juridice',
            href: '#',
          }, {
            name: 'Traduceri Diverse',
            href: '#',
          }, {
            name: 'Interpretare',
            href: '#',
          }],
      }, {
        name: 'Despre mine',
        href: '#',
      }, {
        name: 'Limba',
        href: '#',
        extraClasses: 'align-left',
        children: [
          {
            name: 'Romana',
            href: '#',
          }, {
            name: 'Engleza',
            href: '#',
          }, {
            name: 'Portugheza',
            href: '#',
          }],
      },
    ];

    return (
        <nav id="MenuBar">
          <ul className={'navList clearfix ' + (this.props.menuExpanded ? 'open' : '')}>
            {
              menuTabs.map((tab, index) => <NavItem tab={tab} key={index}/>)
            }
          </ul>
        </nav>
    );
  }
}


