import React, {Component} from 'react';

export default class NavItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      expanded: false,
    };

    this.toggleExpanded = this.toggleExpanded.bind(this);
  }

  toggleExpanded() {
    this.setState(prevState => ({expanded: !prevState.expanded}));
  }

  render() {
    let ulClasses= (this.state.expanded ? ' open ' : '')
        + (this.props.tab.extraClasses ? this.props.tab.extraClasses : '');


    return (
        <li onClick={this.toggleExpanded}>
          <a href={this.props.tab.href}>
            {this.props.tab.name}
            {this.props.tab.children ? <i className="fa fa-angle-down"/> : null}
          </a>
          {
            this.props.tab.children &&
            <ul className={`dropMenu ${ulClasses}`}>
              {this.props.tab.children.map((child, i) =>
                  <li key={i}>
                    <a href={child.href}>{child.name}</a>
                  </li>,
              )}
            </ul>
          }
        </li>

    );
  }
}



